<template>
  <div>
    <div class="nav-bar-white">
      <headerBar :header-data="headerData" />
    </div>
    <van-sticky offset-top="0">
      <!-- 项目信息简介 begin -->
      <div class="fixed-project fit-project fixed-project-color">
        <div class="list">
          <h3>{{ dbxq.processName }}</h3>
          <div class="info">
            <span>
              <i class="iconfont iconlianxiren" />
              {{ dbxq.preUsers }}
            </span>
            <span>
              <i class="iconfont iconshijian" />
              {{ dbxq.showTime }}
            </span>
            <span>
              <i class="iconfont iconshijian" />
              {{ dbxq.taskName }}
            </span>
          </div>
        </div>
      </div>
      <!-- 项目信息简介 end -->
    </van-sticky>
    <div v-for="index in tabs" :key="index.name" :title="index.name">
      <!--<jbrzbxx :appsrvurl="index.appsrvurl" :bizid="dbxq.bizid" :executorid="dbxq.executorid" />-->
      <jbrlzrz v-if="index.appsrvurl == 'lzrz'" :processid="dbxq.processId" />
      <jbrzbxx v-else :appsrvurl="index.appsrvurl" :bizid="dbxq.bizid" :executorid="dbxq.executorid" />
    </div>
    <!--<jbrlzrz />-->
    <!-- <div class="project-tab sp-details">
      <v-nav :list-title="title" :swiper-send-index="swiperToNav" @changeTab="clickIndex" />
      <v-swiper :swiper-index="slideIndex" @slideTab="slideSendNav" />
    </div> -->

    <!--<van-tabs class="van-tabs" :active="active" swipeable>
      <van-tab v-for="index in tabs" :key="index.name" :title=" index.name">
        <jbrzbxx :appsrvurl="index.appsrvurl" />
        <jbrlzrz />
      </van-tab>
    </van-tabs>-->
    <div v-if="lcButton.hasBtn" class="foot-btn fixed">
      <ul>
        <li v-if="lcButton.backToPreBtn.isShow"><van-button type="info" round plain size="large" @click="popupClick(0)">{{ lcButton.backToPreBtn.btnName }}</van-button></li>
        <li v-if="lcButton.backBtn.isShow"><van-button type="info" round plain size="large" @click="popupClick(2)">{{ lcButton.backBtn.btnName }}</van-button></li>
        <li v-if="lcButton.submitBtn.isShow"><van-button type="info" round size="large " @click="popupClick(1)">{{ lcButton.submitBtn.btnName }}</van-button></li>
        <li v-if="lcButton.hqBtn.isShow"><van-button type="info" round size="large " @click="popupClick(3,lcButton.hqBtn.btnName)">{{ lcButton.hqBtn.btnName }}</van-button></li>
      </ul>
    </div>

    <!-- 信息校验popup beign -->
    <van-dialog v-model="showCheck" title="校验信息">

      <div class="content">
        <div class="check-title">您提交的信息有如下错误:</div>
        <div class="check-message">
          <ul>
            <div v-for="(msg, index) in checkMsgs" :key="index">
              <li class="undone">{{ msg.jyx }}</li>
            </div>
          </ul>
        </div>
      </div>

    </van-dialog>
    <!-- 信息校验popup end -->

  </div>
</template>

<script>
// import xyNavJbr from '../../../components/nav/xy-nav-jbr.vue'
import headerBar from '@/components/header/headerBar.vue'
import jbrzbxx from './wddbzbxx.vue'
import jbrlzrz from './wddblzrz.vue'
import { mapState, mapMutations } from 'vuex'
import { getTaskAction, getDataTabCheckMsgsAction, getZxztAction } from '../../../../store/jbr/dbsx.js'
import { Dialog } from 'vant'
import { pageRouter } from '@/common/js/mixin.js'
export default {
  name: 'WddbXxxx',
  components: {
    headerBar,
    jbrzbxx,
    jbrlzrz
    // 'v-nav': jbrspnav,
    // 'v-swiper': jbrspswiper
  },
  mixins: [pageRouter],
  data() {
    return {
      // slideIndex: 0, // 点击导航发送给swiper的值
      // swiperToNav: 0, // swiper滑动下标值传给nav导航
      headerData: {
        title: '',
        leftUrl: 'back',
        leftText: ' '
      },
      tabs: [], // 标题内容
      active: 0,
      // taskId: '',
      dbxq: {},
      lcButton: {},
      checkMsgs: [],
      showCheck: false
      // title: [] // 标题内容
    }
  },
  computed: {
    ...mapState({
      // dbitem: state => state.jbrdbsx.listItem,
      txBizid: state => state.jbrdbsx.txBizid,
      goXmxqMore: state => state.jbrdbsx.goXmxqMore,
      goPdf: state => state.jbrdbsx.goPdf,
      goImages: state => state.jbrdbsx.goImages,
      goHtml: state => state.jbrdbsx.goHtml,
      userInfo: state => state.mh.mhUserInfo
    })
  },
  watch: {
    // 监听导航传给swiper时候，把数据传给父亲时候传参值的变化
    // slideIndex(value) {
    //   // swiper滑动下标值传给nav导航,目的是为了导航的下标和swiper的下标索引能时时同步不存在bug
    //   this.swiperToNav = value
    // },
    // swiperToNav(value) {
    //   // 点击导航发送给swiper的值,目的是为了导航的下标和swiper的下标索引能时时同步不存在bug
    //   this.slideIndex = value
    // },
    goXmxqMore: function(old) {
      // this.temp = old
      if (old === '1') {
        this.$router.push({
          name: 'sjbqmore'
        })
      }
    },
    goPdf: function(old) {
      // this.temp = old
      if (old !== '') {
        this.SET_GO_PDF('')
        this.$router.push({
          name: 'pdf',
          params: { url: old }
        })
      }
    },
    goImages: function(old) {
      // this.temp = old
      if (old !== '') {
        this.SET_GO_IMG('')
        this.$router.push({
          name: 'GgImages',
          params: { url: old }
        })
      }
    },
    goHtml: function(old) {
      // this.temp = old
      if (old !== '') {
        this.SET_GO_HTML('')
        this.$router.push({
          name: 'GgHtml',
          params: { url: old }
        })
      }
    }
  },

  created() {
    this.headerData.title = this.$route.params.title
    this.getTaskData()
  },
  methods: {
    ...mapMutations([
      'SET_GO_PDF',
      'SET_GO_IMG',
      'SET_GO_HTML',
      'jbrdbsx/SET_Refresh'
    ]),
    getTaskData() {
      console.log('executorid:', this.txBizid)
      getTaskAction({
        executorid: this.txBizid
      }).then(res => {
        console.log('task:', res)
        if (res.length === 0) {
          this.$toast.fail('暂无数据！')
        } else {
          this.dbxq = res
          const sjbqs = JSON.stringify(this.dbxq.datatabs)
          const sjbqList = JSON.parse(sjbqs)
          for (let i = 0; i < sjbqList.length; i++) {
            var datas = { 'name': sjbqList[i].name, 'appsrvurl': sjbqList[i].appsrvurl, 'appurl': sjbqList[i].appurl }
            this.tabs.push(datas)
          }

          this.lcButton = this.dbxq.lcButton

          var hasLzrz = this.lcButton.logBtn.isShow
          if (hasLzrz) {
            var lzrz = { 'name': '流转日志', 'appsrvurl': 'lzrz', 'appurl': 'lzrz' }
            this.tabs.push(lzrz)
          }
        }
      })
    },
    goBack() {
      this.$router.push('/jbrsphome')
    },
    // goMore() {
    //   this.$router.push({
    //     name: 'sjbqmore'
    //   })
    // },

    popupClick(type, btnMc) {
      this.$store.dispatch('jbrdbsx/setDbsxspxz', type)
      getZxztAction({
        id: this.txBizid
      }).then(res => {
        if (res.data === 2) {
          if (type === 0) {
            if (this.dbxq.thyjbt === null || this.dbxq.thyjbt === 3 || this.dbxq.thyjbt === 2) {
              this.clickBackOneStepSP()
            } else {
              this.$router.push({
                name: 'backonestep'
              })
            }
          } else if (type === 1) {
            console.log('this.dbxq.executorid:', this.dbxq.executorid)
            getDataTabCheckMsgsAction({
              executorid: this.dbxq.executorid
            }).then(res => {
              console.log('datatab msgs:', res)
              if (res.length === 0) {
                if (this.dbxq.tjyjbt === 2) {
                  this.clickSubmitSP()
                } else {
                  this.$router.push({
                    name: 'submit'
                  })
                }
              } else {
                this.checkMsgs = res
                this.showCheck = true
              }
            })
          } else if (type === 2) {
            if (this.dbxq.thyjbt === null || this.dbxq.thyjbt === 2) {
              this.clickBackSP()
            } else {
              this.$router.push({
                name: 'back'
              })
            }
          } else {
            this.$router.push({
              name: 'hq',
              params: { btnMc: btnMc }
            })
          }
        } else {
          Dialog.alert({
            message: '该任务已提交！'
          }).then(() => {
            this.SET_Refresh('true')
          })
        }
      })
    },
    clickSubmitSP() {
      Dialog.confirm({
        message: '是否提交当前任务？'
      }).then(() => {
        this.$store.dispatch('jbrdbsx/sendSP', {
          id: this.txBizid,
          dqjgid: this.userInfo.zzid,
          dqryid: this.userInfo.id
        }).then(res => {
          this.$toast('审批成功！')
          setTimeout(() => {
            this.$router.push('/DbsxIndex')
            this.SET_Refresh('true')
          }, 2000)
        }).catch(error => {
          // this.pageStyles = 3
          this.$toast(error)
        })
      })
    },
    clickBackSP() {
      Dialog.confirm({
        message: '是否退回当前任务？'
      }).then(() => {
        this.$store.dispatch('jbrdbsx/sendBackSP', {
          executorid: this.txBizid
          // eslint-disable-next-line no-unused-vars
        }).then(res => {
          this.$toast('退回成功！')
          setTimeout(() => {
            this.$router.push('/DbsxIndex')
            this.SET_Refresh('true')
          }, 2000)
        }).catch(error => {
          // this.loading = false
          // this.pageStyles = 3
          this.$toast(error)
        })
      })
    },
    clickBackOneStepSP() {
      Dialog.confirm({
        message: '是否退回到上一步？'
      }).then(() => {
        this.$store.dispatch('jbrdbsx/sendBackOneStepSP', {
          id: this.txBizid
        }).then(res => {
          this.$toast('退回成功！')
          setTimeout(() => {
            this.$router.push('/DbsxIndex')
            this.SET_Refresh('true')
          }, 2000)
        }).catch(error => {
          // this.loading = false
          // this.pageStyles = 3
          this.$toast(error)
        })
      })
    },
    // 点击导航菜单通过emit传给父亲然后父亲再传给子组件swiper组件
    clickIndex(index) {
      this.slideIndex = index
      // this.$store.commit('saveNavIndex', index)
    },
    // swiper滑动后下标通过emit传给父亲。父亲再传给子组件nav
    slideSendNav(index) {
      this.swiperToNav = index
      // this.$store.commit('saveSwiperIndex', index)
    }
  }
}
</script>

<style lang="css" scoped>
  .van-tabs{padding-top: 136px}
  .fixed-project{box-shadow: none;}
</style>
