<template>
  <div class="content">

    <div class="common-main box">
      <div class="title">
        <div class="title-left">
          <h2>流转日志</h2>
        </div>
      </div>
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad"
      >
        <div class="vertical-container-sprz">
          <div class="vertical-timeline-block lcsp-done">
            <div class="vertical-timeline-icon" />
            <div class="vertical-timeline-content">
              <h4>开始</h4>
            </div>
          </div>

          <div v-for="(item,index) in loglist" :key="index" :class="getStatus(item.status) " class="vertical-timeline-block ">
            <div class="vertical-timeline-icon">
              <i class="iconfont iconduihao" />
            </div>
            <div class="vertical-timeline-content">
              <h4>
                {{ item.taskName }}
                <van-tag v-if="item.status== 2" plain type="primary">进行中</van-tag>
                <van-tag v-else-if="item.status== 3 && item.backStatus== 2" plain type="danger">退回</van-tag>
                <van-tag v-else-if="item.status== 3" plain type="success">已完成</van-tag>
                <!-- <div v-else-if="item.status== 1" class="xy-tag success solid"></div> -->
                <!-- <i class="iconfont iconarrow-up"></i> -->
              </h4>
              <div v-if="item.status != 1" class="vertical-timeline-hide">
                <p>
                  <span class="sprz-name"> {{ item.userName }}</span>
                  <span class="sprz-info">{{ item.preDepts }}</span>
                  <!-- <span class="sprz-stay"> <em>停留</em>17天8小时</span> -->
                </p>
                <p>
                  <span class="sprz-date"><em>送达时间</em> {{ item.createTime | formatTime14 }} </span>
                  <span class="sprz-date"><em>提交时间</em> {{ item.submitTime | formatTime14 }}</span>
                </p>
                <div class="text-detail">
                  <span class="sprz-title">{{ item.taskCommentLabel }}：</span>{{ item.taskComment }}
                </div>

              </div>
            </div>
          </div>

          <div class="vertical-timeline-block lcsp-undone">
            <div class="vertical-timeline-icon" />
            <div class="vertical-timeline-content">
              <h4>结束</h4>
            </div>
          </div>

        </div>
        <div class="common-space" />
      </van-list>
    </div>
  </div>

</template>

<script land="script" scoped>
// import commonList from '@/components/list/commonList.vue'
import { mapState } from 'vuex'
import constants from '../../../../common/js/constants'

export default {
  name: 'One',
  components: {
    // commonList
  },
  props: {
    zxrid: {
      type: String,
      default: ''
    },
    processid: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      ind: '', // 点击事件
      loglist: [],
      loading: false,
      finished: false,
      jbrdballPage: {
        pageStyles: 0, // 页面状态展示
        pageTitle: ''
      }
    }
  },
  computed: {
    ...mapState({
      userInfo: state => state.user.userInfo
      // dbitem: state => state.jbrdbsx.listItem
    })
  },
  created() {
    this.XMZT = constants.XMZT
    this.CGFS = constants.CGFS
    this.TimeDay = constants.TimeDay
    this.pageno = 1
    // this.getlzrzlist(this.pageno)
  },
  methods: {
    changeBgc: function(index) {
      this.ind = index
    },
    // $_onRefresh() {
    //   this.pageno = 1
    //   this.getlzrzlist(this.pageno)
    // },

    getStatus(status) {
      switch (status) {
        case 1: return 'lcsp-undone'
        case 2: return 'lcsp-done lcsp-current'
        case 3: return 'lcsp-done'
      }
    },

    onLoad() {
      this.$store.dispatch('jbrdbsx/getlzrzlistActions', {
        pageno: this.pageno,
        id: this.processid,
        query: ''
      }).then(res => {
        this.pageno = res.pageno
        this.totalpageno = res.totalpageno
        if (res.totalpageno === 0) {
          this.jbrdballPage.pageStyles = 1
          this.finished = true
        } else {
          if (this.pageno === 1) {
            if (res.data != null && res.data !== '') {
              this.loglist = []
              this.loglist = res.data
            }
          } else {
            if (res.data != null && res.data !== '') {
              this.loglist = this.loglist.concat(res.data)
            }
          }
        }
        if (res.totalpageno <= res.pageno) {
          this.finished = true
        }
      }).catch(error => {
        this.jbrdballPage.pageStyles = 2
        console.log('error=====', error)
      })
    }

  }
}
</script>

<style lang="css" scoped>
   .space{height:50px;}
</style>
