<template>
  <div class="content">
    <div class="common-main box">
      <!--<div class="container-mini">-->
      <div
        v-for="(item, index) in homedata"
        :key="index"
        class="list"
      >
        <!-- 单表 begin -->
        <div v-if="item[0] === 1">
          <div class="title title-page">
            <div class="title-left">
              <h2>{{ item[1] }}</h2>
            </div>
          </div>
          <div v-for="(items, index) in item[2]" :key="index">
            <div class="page-list">
              <div class="pagelist-left">{{ items.name }}</div>
              <div v-if="items.type === 1" class="pagelist-right">{{ items.value }}</div>
              <!-- 单附件 begin -->
              <div v-else-if="items.type === 9" class="pagelist-right  ">
                <div v-if="items.file !== ''&& items.file !== null " @click="getFile(items.type,items.file,items.value)">
                  <!-- <i class="iconfont iconxiazai"></i> -->
                  {{ items.value }}
                </div>
                <div v-else>
                  <ul v-for="(item, indexs) in items.values" :key="indexs">
                    <li @click="getFiles(items.type,items.files[indexs])">
                      <!-- <i class="iconfont iconxiazai"></i> -->
                      {{ item }}
                    </li>
                  </ul>
                </div>
              </div>
              <!-- 单附件 end -->
              <!-- 多附件 begin -->
              <div v-else class="pagelist-right  blue">
                <div v-if="items.file !== ''&& items.file !== null " @click="getFile(items.type,items.file,items.value)">
                  <i class="iconfont iconxiazai" />
                  {{ items.value }}
                </div>
                <div v-else>
                  <ul v-for="(item, indexs) in items.values" :key="indexs">
                    <li @click="getFiles(items.type,items.files[indexs],items.values[indexs])">
                      <i class="iconfont iconxiazai" />
                      {{ item }}
                    </li>
                  </ul>
                </div>
              </div>
              <!-- 多附件 end -->
            </div>
            <!-- <div class="pagelist-left"  v-if="items.type === 9">附件</div> -->
          </div>
        </div>
        <!-- 单表 end -->
        <!-- 多表 begin -->
        <div v-else>
          <div class="seg-line" />
          <div v-for="(itemws, page1) in item[2]" :key="page1">
            <div v-if="page1 == 0" class="title title-page">
              <div class="title-left">
                <h2>{{ item[1] }}</h2>
              </div>
            </div>
            <div v-for="(sj, page2) in itemws" :key="page2" class="page-list">
              <div class="pagelist-left">{{ sj.name }}</div>
              <div v-if="sj.type === 1" class="pagelist-right">{{ sj.value }}</div>
              <!-- 单附件 begin -->
              <div v-else-if="sj.type === 9" class="pagelist-right  ">
                <div v-if="sj.file !== ''&& sj.file !== null " @click="getFile(sj.type,sj.file,sj.value)">
                  {{ sj.value }}
                </div>
                <div v-else>
                  <ul v-for="(item, indexs) in sj.values" :key="indexs">
                    <li @click="getFiles(sj.type,sj.files[indexs])">
                      {{ item }}
                    </li>
                  </ul>
                </div>
              </div>
              <!-- 单附件 end -->
              <!-- 多附件 begin -->
              <div v-else class="pagelist-right  blue">
                <div v-if="sj.file !== ''&& sj.file !== null " @click="getFile(sj.type,sj.file,sj.value)">
                  <i class="iconfont iconxiazai" />
                  {{ sj.value }}
                </div>
                <div v-else>
                  <ul v-for="(item, indexs) in sj.values" :key="indexs">
                    <li @click="getFiles(sj.type,sj.files[indexs],sj.values[indexs])">
                      <i class="iconfont iconxiazai" />
                      {{ item }}
                    </li>
                  </ul>
                </div>
              </div>
              <!-- 多附件 end -->
              <!--<div class="pagelist-left">{{ sj.name }}</div>
              <div v-if="page2 === 0" class="pagelist-right ">
                {{ sj.value }}
                &lt;!&ndash; <md-icon name="arrow-right" size="sm"></md-icon> &ndash;&gt;
              </div>
              <div v-else class="pagelist-right">{{ sj.value }}</div>-->
            </div>
            <div class="page-list">
              <div class="pagelist-left" />
              <!-- <div class="pagelist-right more">查看详情<md-icon name="arrow-right" size="sm"></md-icon></div> -->
            </div>
            <div class="seg-line" />
          </div>
          <!--<div class="page-more"  showmore  @click="goMore(index)">
            查看更多信息
            <i class="iconfont iconarrowright"></i>
          </div>-->
        </div>
        <!-- 多表 end -->
      </div>

      <!-- 空白区域，撑高度 -->
      <div class="sub-space" />
      <!--</div>-->
    </div>
  </div>
</template>

<script land="script" scoped>
import { mapState, mapMutations } from 'vuex'
// import commonList from '@/components/list/commonList.vue'
export default {
  name: 'One',
  components: {
    // commonList
  },
  props: {
    appsrvurl: {
      type: null,
      required: true
    },
    executorid: {
      type: null,
      required: true
    },
    bizid: {
      type: null,
      required: true
    }
  },
  data() {
    return {
      ind: '', // 点击事件
      pageUrl: '',
      jbrzdxxPage: {
        pageStyles: 1, // 页面状态展示
        pageTitle: ''
      },
      list: {
        items: [],
        error: false, // 是否在刷新
        postUrl: '',
        reqData: '',
        dataName: '',
        noDataText: ''
      },
      homedata: [],
      showMoreList: [],
      isLoading: false,
      finished: true,
      loading: false
    }
  },
  computed: {
    ...mapState({
      dbitem: state => state.jbrdbsx.listItem
      // jbrsjbqList: state => state.jbrdbsx.jbrsjbqList
    })
  },
  watch: {
    // jbrsjbqList(val) {
    //   this.listData = val
    //   this.$_onRefresh()
    // }
  },
  mounted() {
    window.ScrollViewTrigger1 = () => {
      // this.$refs.scrollView.triggerRefresh()
    }
  },
  created() {
    // this.listData = this.jbrsjbqList
    this.getzbxxData()
    // this.$on('sjbqurl', (sjbqurl) => {
    //   console.info('=====sjbqurl=====', sjbqurl)
    // })
    // this.$_onRefresh()
    // this.getzbxxData()
    // this.getPageData()
  },
  methods: {
    ...mapMutations([
      'jbrdbsx/SET_XMXQ_MORE',
      'jbrdbsx/SET_LOAD_MORE',
      'jbrdbsx/SET_GO_PDF',
      'jbrdbsx/SET_GO_IMG',
      'jbrdbsx/SET_GO_HTML'
    ]),
    changeBgc: function(index) {
      this.ind = index
    },

    getFile(type, url) {
      if (type === 10) { // pdf
        // this.SET_GO_PDF(url)
        window.location('/app/static/pdf/web/viewer.html?file=' + url)

        // this.SET_GO_IMG(type)
      } else if (type === 11) { // image
        // this.$emit('go-image', url)
        this.SET_GO_IMG(url)
      } else if (type === 12) { // html
        // this.$emit('go-html', url)
        this.SET_GO_HTML(url)
      }
    },
    /* 判断当前文件后后缀名称 */
    getFilesType(value) {
      // const test = '竞谈-全电子-0821001-zxj（方案转入）招标文件.pdf'
      const last_len = value.lastIndexOf('.')
      const len = value.length
      const types = value.substring(last_len, len)
      if (types === '.pdf' || types === '.html' || types === '.jpg' || types === '.png' || types === '.jpg' || types === '.jpeg') {
        return true
      } else {
        return false
      }
    },
    // 1:普通文本  2：日期时间 3 文本html  打开附件 10-PDF文件 11-图片文件 12-HTML文件
    getFiles(type, url, value) {
      const last_len = value.lastIndexOf('.')
      const len = value.length
      const types = value.substring(last_len, len)
      // pathf = url.substring(last_len, len)
      if (types === '.pdf') { // pdf
        window.open('/app/static/pdf/web/viewer.html?file=' + url)
      } else if (types === '.jpg' || types === '.png' || types === '.jpeg') { // image
        // this.$emit('go-image', url)
        this.SET_GO_IMG(url)
      } else if (types === '.html') { // html
        // this.$emit('go-html', url)
        this.SET_GO_HTML(url)
      } else {
        this.$toast.fail('暂不支持该附件类型')
      }
    },
    // 跳转更多页面
    goMore(index) {
      // this.SET_XMXQ_MORE('1')
      // console.info('index==3==', this.homedata[index - 1].showmore)
      // console.info('index==4==', this.homedata[index - 1].title)
      const more = []
      if (this.showMoreList[index].length > 0) {
        more.push(this.showMoreList[index])
        more.push(this.homedata[index - 1].title)
        this.$emit('goXmxqMore', null)
        // this.SET_LOAD_MORE(more)
      } else {
        this.$toast('暂无更新信息')
      }
      // console.info('index==1==', this.datas[index - 1].showmore)
    },
    onLoad() {
      this.getzbxxData()
    },
    getzbxxData() {
      console.log('wddbzbxx:', this.appsrvurl, this.bizid, this.executorid)
      this.$store.dispatch('jbrdbsx/getjbrZbxxAction', {
        url: this.appsrvurl,
        bizid: this.bizid,
        executorid: this.executorid // 单点登录传过来的执行人id
      }).then(res => {
        const array = []

        for (let i = 0; i < res.length; i++) {
          const testarray = []
          if (res[i].type === 1) {
            for (let k = 0; k < res[i].data.length; k++) {
              if (k === 0) {
                array.push(1)
                array.push(res[i].title)
                this.showMoreList.push('')
              }
              testarray.push(res[i].data[k])
            }
            array.push(testarray)
            this.homedata.push(array)
          } else if (res[i].type === 2) {
            const dbArray = []
            const test2 = []
            if (res[i].data.length > 0) {
              for (let j = 0; j < res[i].data.length; j++) {
                if (j === 0) {
                  dbArray.push(2)
                  dbArray.push(res[i].title)
                  this.showMoreList.push(res[i].showmore)
                }
                test2.push(res[i].data[j])
              }
              dbArray.push(test2)
              this.homedata.push(dbArray)
            } else {
              // dbArray.push(res[i].title)
              // this.homedata.push(dbArray)
            }
          }
        }
      }).catch(error => {
        // this.loading = false
        // this.pageStyles = 3
        this.$toast(error)
      })
      // } else {
      //   this.$toast('路径数据为空')
      // }
    }

  }

}
</script>
<style lang="css" scoped>
  .jrzbxx-page{height:200px}
  .common-list{height:200px}
</style>
